import React from 'react';
import './App.css';
import Warper from './Warper';
import Popup from 'reactjs-popup';

const contentStyle = {
  maxWidth: '600px',
  width: '90%',
};

const ApsideExperience = () => (
  <Popup
    trigger={
      <h3 class="mb-0">
        Stagiaire Ingnieur Infrastructures/Systmes
      </h3>
    }
    modal
    lockScroll={true}
    contentStyle={contentStyle}
    nested
  >
    {close => (
      <div className="modal-content">
        <div class="modal-header">
        <h3 class="modal-title" id="exampleModalLabel">Stagiaire Ingnieur Infrastructures/Systmes</h3>
        <button type="button" class="close" data-dismiss="modal" aria-label="Close" onClick={close}>
          <span aria-hidden="true">&times;</span>
        </button>
      </div>
        <div className="modal-body">
          <h4 class="modal-title" id="exampleModalLabel">Contexte</h4>
          <div className="context-body">
          Projet personnel encadr : Cration dune application web afin de centraliser et exploiter les donnes rseaux et scurit prsentes sous Microsoft Azure au sein dun projet de migration de tout un datacenter vers Microsoft Azure.
          </div>
          <h4 class="modal-title" id="exampleModalLabel">Ralisations</h4>
          <h6 style={{color:'rgb(31,79,106)'}}>Cration de l'application WEB</h6>
          <ul>
                <li>Ralisation puis ajustement du cahier des charges avec l'avancement de la migration</li>
                <li>Planification de runions, discussions avec les interlocuteurs ncessaires  l'avancement et  la cohrence du projet</li>
                <li>Suivi de la migration ralise par l'quipe  travers les runions quotidiennes pour une volution de l'application en temps rel ainsi qu'une monte en comptence sur Microsoft Azure et AzureDevOps</li>
          </ul>
          <h6 style={{color:'rgb(31,79,106)'}}>Elements de l'application</h6>
          <ul>
                <li>Une page d'accueil recensant les dernires modifications, les dernires alertes et la quantit des quipements</li>
                <li>Une cartographie des quipements sous Microsoft Azure</li>
                <li>Une liste des rgles de scurit avec une intelligence artificielle permettant de dtecter des doublons, des rgles essentielles manquantes ou des rgles trop larges</li>
                <li>Une fonctionnalit permettant de tester une communication entre deux quipements : test de la connexion, chemin emprunt et rgles trouves. Il y a aussi une intelligence permettant de dtecter une opposition des rgles ou la prsence d'une mme rgle avec des priorits diffrentes sur le chemin parcouru</li>
                <li>Une authentification Microsoft restreint aux personnels autoriss</li>
          </ul>
        </div>
        
      </div>
    )}
  </Popup>
);

export default Warper(ApsideExperience);
